import React, {useCallback, useState} from 'react';
import './App.scss';
import { Check } from '@mui/icons-material';
import classNames from 'classnames';
import { GlassMagnifier } from 'react-image-magnifiers';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField, useMediaQuery,
  useTheme,
  ThemeProvider,
  createTheme, Paper
} from '@mui/material';
import appPreviewLarge from './img/phone-smaller.png';
import phoneReflection from './img/phone_reflection.png';
import playStore from './img/en_badge_web_generic.png';
import appStore from './img/appstore.svg';

const theme = createTheme({palette: {primary: {main: '#DE9433'}}});

function App() {
  return <ThemeProvider theme={theme}>
    <Landing />
  </ThemeProvider>
}


function Landing() {
  const [waitListDialogOpen, setWaitListDialogOpen] = useState(false);
  const [email, setEmail] = useState('');
  const [registered, setRegistered] = useState(false);

  const registrationClicked = useCallback(async (email: string) => {
    setWaitListDialogOpen(false);
    setRegistered(true);
  }, []);

  const theme = useTheme();
  const isPhone = useMediaQuery(theme.breakpoints.down('md'))
  const fullScreenDialogs = isPhone;
  return (
    <div className="app">
      <div className="app-bg">
          <div className="bg-image" />
          <div className="bg-image-fade" />
      </div>
      <div className="app-layout">
        <div className="basad">
          בס"ד
        </div>
        <div className="logo-container">
          <div className="logo-image" />
          <div className="logo-name" />
        </div>
        <div className="tagline"><div className="finally">סוף סוף -&nbsp;</div><div className="app-that-understands">בית חכם <b>שמבין אותך</b></div></div>
        <div className="phone">
          {isPhone
            ? <img className="phone-preview" src={appPreviewLarge} alt="" />
            : <GlassMagnifier className="phone-preview" imageSrc={appPreviewLarge} imageAlt='screenshot'
                              magnifierSize='200%' allowOverflow={true} />}
          <img className="phone-reflection" src={phoneReflection} />
        </div>
        <div className="description">
          <div className="description-text">אפליקציית הבית החכם לשומרי שבת</div>
          {!registered && <div className="store-buttons">
              <img className={classNames('app-store-icon', {disabled: registered})} src={appStore}
                   onClick={() => { if (!registered) setWaitListDialogOpen(true); }} />
              <img className={classNames('play-store-icon', {disabled: registered})} src={playStore}
                   onClick={() => { if (!registered) setWaitListDialogOpen(true); }} />
          </div>}
          {registered && <Paper className='thanks'>
            <Check className='thanks-icon' />
            <div className='thanks-text'><b>תודה!</b>&nbsp;נעדכן אתכם כאשר האפליקציה תהיה זמינה עבורכם להורדה!</div>
          </Paper>}
        </div>
      </div>
      <Dialog open={waitListDialogOpen} onClose={() => setWaitListDialogOpen(false)} fullScreen={fullScreenDialogs} >
        <DialogContent>
          <DialogContentText>
            <div className='spots'>
              <div className='spots-text'>150/150</div>
              <div className='spots-label'>מקומות</div>
            </div>
            <b>לא נותרו כרגע מקומות</b>             מאחר שאנחנו עוד בשלבי הרצה ראשוניים
            <br /><br />
            הכניסו את כתובת המייל וניצור עמכם קשר כאשר יתפנו עוד מקומות!
          </DialogContentText>
          <TextField
            autoFocus
            placeholder="Email"
            type="email"
            name="email"
            value={email}
            margin="dense"
            onChange={ev => setEmail(ev.target.value)}
            fullWidth
            variant="standard"
          />
        </DialogContent>
        <DialogActions>
          {fullScreenDialogs && <Button variant='text' onClick={() => setWaitListDialogOpen(false)}>
            ביטול
          </Button>}
          <input type="hidden" name="name" placeholder="Saar Raz"/>
          <input type="hidden" name="_replyto" placeholder="saarraz1@gmail.com"/>
          <input type="hidden" name="message" value={email} />
          <input type="hidden" name="_subject" value="Contact form submitted"/>
          <input type="hidden" name="_after" value="https://myhomepage.net/"/>
          <input type="hidden" name="_honeypot" value=""/>
          <input type="hidden" name="_confirmation" value=""/>
          <Button disabled={!email} variant='contained' onClick={() => registrationClicked(email)}>
            יידע אותי כשניתן להוריד
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default App;
